@import "../global.scss";

.portfolio {
    background-color: transparent;
    display: flex;
    flex-direction: column;
    align-items: center;
    // justify-content: center;

    h1 {
        font-size: 50px;
        margin: 50px;

        @include moblie{
            font-size: 40px;
        }
    }


    ul {
        margin: 10px;
        padding: 0;
        list-style: none;
        display: flex;

        @include moblie{
            flex-wrap: wrap;
            justify-content: center;
        }
    }

    .container{
        width: 70%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        @include moblie{
            width: 100%;
            margin: 10px 0;
        }

        .item{
            width: 220px;
            height: 150px;
            border-radius: 20px;
            border: 1px solid lightgray;
            margin: 10px 20px;
            display: flex;
            align-items: center;
            justify-content: center;
            color: white;
            position: relative;
            transition: all 0.5s ease;
            cursor: pointer;

            @include moblie{
                width: 130px;
                height: 100px;
            }

            h3{
                position:absolute;
            }

            img{
                width: 100%;
                height: 100%;
                object-fit: cover;
                z-index: 1;
            }

            &:hover{
                background-color: $subColor2;
                img{
                    opacity: 0.2;
                    z-index: 0;
                }
            }
        }
    }
}
